<template lang="pug">
  .add-comment
    textarea(v-model='text', placeholder='Ваше сообщение')
    .footer
      .user(style='background-image: url("data:image/jpeg;base64,")')
      s-btn(:disabled='!text', @click='sendComment') Отправить
</template>
<script>
import ru from '../../../../node_modules/date-fns/locale/ru'
import { format } from 'date-fns'

export default {
  data () {
    return {
      text: ''
    }
  },
  methods: {
    sendComment: function () {
      const body = {
        text: this.text,
        date: format(
          new Date(),
          'D MMM HH:mm:ss',
          {locale: ru}
        )
      }
      this.$store.commit('addComment', body)
      this.text = ''
    }
  }
}
</script>

<style lang="sass" scoped>
.add-comment
  margin-top: 21px
  textarea
    width: 100%
    height: 90px
    background-color: #f8fafb
    font-size: 14px
    border-radius: 4px
    padding: 13px 19px
    box-sizing: border-box
    resize: none
    &::placeholder
      color: #98a9bc
      font-weight: 500
  .footer
    display: flex
    justify-content: space-between
    margin-top: 11px
    .user
      width: 36px
      height: 36px
      background-position: center center
      background-size: 100% auto
      border-radius: 50%
</style>
